@import url(https://fonts.googleapis.com/css2?family=Gochi+Hand&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-image: url("http://hajdeda.com/docs/dynuploaded07-24-2018_102006_27597.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  font-family:  'Gochi Hand', cursive;
}

.links{
  border: 2px solid rgb(148, 197, 243);
  background-color:rgba(148, 197, 243, 0.685);
  padding: 2px;
  font-size: 1.5em;
  margin:0;
  display: flex;
  justify-content: left;

}
.links:hover {
  background-color: rgb(148, 197, 243);
}
.link {
  text-align: center;
  text-decoration: none;
  font-weight: bold;
  padding: 10px;
  color: darkblue;
}
.link:hover{
  color: darkviolet;
}



.drinksFun {
  color: darkblue;
  font-size: 2em;
  text-align: center;
  font-weight: bolder;
  font-style: oblique;
}
input {
    margin: 5px;
    height: 35px;
    border-radius: 5px;

}
.registerButton {
    height: 30px;
}
.button1 {
    max-height: 50px;
    max-width: 150px;
    border-radius: 5px;
    font-size: 0.90em;
    margin-right: 5px;
}
.linkRegister {
    color:darkblue;
    font-weight: bold;
    margin-right: 5px;
}
.linkRegister:hover{
    color: darkviolet;
    
}
.register {
    display: flex;
    flex-flow: column;
    max-width: 300px;
    background-color:rgba(148, 197, 243, 0.685);
    margin: 0% auto;
    border-radius: 5px;
    margin-top: 20px;
    border: 2px solid rgb(148, 197, 243);
    
}
.register:hover {
    background-color: rgb(148, 197, 243);
  }
.login {
    display:flex;
    flex-flow: row-reverse;
    
    background-color:rgba(148, 197, 243, 0.685);
    margin-top: 20px;
    padding: 2px;
    padding-right: 5px;
    border-top: 2px solid rgb(148, 197, 243);
    

    
}

.register1 {
    display: flex;
    flex-flow: row-reverse;
    background-color:rgba(148, 197, 243, 0.685);
    padding-right: 5px;
    font-size: 1em;
    border-bottom: 2px solid rgb(148, 197, 243);
}

.alert1 {
    background-color: white;
    text-align: center;
    border: 2px solid rgb(148, 197, 243);
    color: red;
}

.welcome {
    text-align: center;
    font-size: 2em;
    font-style: oblique;
    /* font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif; */
    font-weight: 900;
    color:darkblue;
    /* max-width: 300px; */
    margin: 30px;
    padding: 20px;
    border-radius: 5px;
    border: 2px solid rgb(148, 197, 243);
}
.all {
    display: flex;
    justify-content: center;
    margin: 30px;
}
.sliderr {
   
    justify-content: center;
    max-width: 800px;
    text-align: center;
}
.sliderImg {
    max-width: 600px;
    max-height: 350px;
    margin: 0% auto;
    border: 2px solid rgb(148, 197, 243);
      
    border-radius: 3px;
}
.drinkList {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    text-align: center;
}
.drink {
    display:flex;
    flex-flow: column;
    text-align: center;
    justify-items: center;
    width: 230px;
    height: 250px;
    font-size: 0.90em;
    margin: 20px;
    background-color: whitesmoke;
    padding: 10px;
    border: 2px solid rgb(148, 197, 243);
    border-radius: 5px;
}
.listImg {
    max-width:  200px;
    max-height: 250px;
    margin: 0% auto;
    border: 2px solid rgb(148, 197, 243);
    border-radius: 5px;
}

.select {
    display:flex;
    justify-content: center;
    background-color: rgba(148, 197, 243, 0.685);
    margin-top: 10px;
    margin-bottom: 10px;
    padding: 10px;
    border: 2px solid rgb(148, 197, 243);
    
}
.select:hover {
    background-color: rgb(148, 197, 243);
  }
.selectOption {
    margin-right: 20px;
    height: 40px;
}
.selectButton {
    max-height: 30px;
    max-width: 100px;
    border-radius: 5px;
    font-size: 0.90em;
}
.pagination {
    display: flex;
    list-style-type: none;
    justify-content: center;
    
}
.page-item{
    margin: 5px;
    color: darkblue;
    font-size: 1em;
}
.page-link {
    text-decoration: none;
    font-weight: bolder;
    color: darkblue;
    font-size: 1em;


}
.logOut {
    display: flex;
    flex-flow: column;
    width: 300px;
    background-color: rgba(148, 197, 243, 0.685);
    margin: 0% auto;
    border-radius: 5px;
    margin-top: 30px;
    padding: 30px;
    border: 2px solid whitesmoke;
}
.logOut:hover {
    background-color: rgb(148, 197, 243);
  }
.thankYou {
    background-color: whitesmoke;
    color: darkblue;
    text-align: center;
    padding: 20px;
    border: 2px solid rgb(148, 197, 243);
    font-size: 1.5em;
    font-weight: bolder;
}
.logoutBtn {
    height: 30px;
}
.blue {
background-color: aliceblue;
}
.grey {
    background-color: lightgray;
}
.listOfCoctails {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;

}
.coctail {
    display:flex;
    flex-flow: column;
    text-align: center;
    justify-items: center;
    width: 330px;
    height: 450px;
    font-size: 0.70em;
    margin: 20px;
    background-color: whitesmoke;
    padding: 10px;
    border: 2px solid rgb(148, 197, 243);
    border-radius: 5px;
}



.coctailImg {
    width: 250px;
    height: 250px;
    margin: 0% auto;
    border-radius: 5px;
}
.alert {

background-color: whitesmoke;
    border-radius: 5px;
    margin: 0% auto;
    width: 300px;
    border: 2px, solid,  rgb(148, 197, 243);
    color:blue;
    font-weight: bolder;
    font-size: 1em;
    text-align: center;}
.name {
    font-size: 1em;
    background-color:rgb(148, 197, 243);
    border-radius:5px;
}
.search
{
    display:flex;
    justify-content: center;
    background-color: rgba(148, 197, 243, 0.685);
    margin-top: 10px;
    margin-bottom: 10px;
    padding: 5px;
    border: 2px solid rgb(148, 197, 243);
    
}
.search:hover {
    background-color: rgb(148, 197, 243);
  }
.searchName{
    width: 200px;
    text-align: center;
}


